.header{
  height: 200px;
  width: 100%;
  background-color : #E9EEF6;
  padding-top: 50px;
}

.card-wrapper{
  transition: 1s ease-in-out;
}

.card-wrapper{
  width: 30%;
  margin: 20px;
  height: 200px; 
  box-shadow: 0px 3px 50px #A5A5A5;
  display: flex;
  flex-direction: column;
}

.card-top{
  width: 100%;
  height: 2%;
}

.task-holder{
  width: 100%;
  height: 98%;
  padding: 10px 10px;
  display : flex;
  flex-direction: column;
  position: relative;
}

.App{
  position: relative;
}

.fixed-copy{
  font-size: 14px;
  color: red;
  font-weight: bold;
  border-radius: 10px;
  border: 1px solid black;
  padding: 5px;
  display: inline-block;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.card-header{
  margin-top: 10px;
  margin-bottom: 10px;
  max-width: 80px;
  height: 30px;
  padding: 1px 1px !important;
  text-align: center;
}

.task-container{
  margin: 0 auto;
  height: 600px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: #F6F7F8;
  padding : 40px 100px;
}

@media(max-width: 800px){
  .card-wrapper{
    width: 100%;
    margin-bottom: 10px;
  }

  .task-container{
    padding : 10px 20px;
  }
}